import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			name: '课堂报告',
			redirect: '/views/classroomreport'
		}, {
			path: '/views',
			name: '视图容器',
			component: resolve => require(['@/views/views'], resolve),
			redirect: '/views/classroomreport',
			children: [
				{
					path: 'classroomreport',
					name: '课堂报告',
					component: resolve => require(['@/views/classroomreport/classroomreport'],resolve)
				},
				{
					path: 'test',
					name: 'demo测试',
					component: resolve => require(['@/views/classroomreport/test'],resolve)
				},
			]
		}, {
			path: '*',
			name: '404页面',
			component: resolve => require(['@/404/404'],resolve)
		}
	]
})



